.friendList{
     display: inline-block;
   
    justify-content: center;
    background: #ffffff;
    
}
.item{
    display: flex;
    text-align: center;
    justify-content: center;
    width: 600px;
    padding: 30px; 
    margin-bottom: 10px;
    background-color: cornflowerblue;
    box-shadow: 0px 2px 1px rgba(0, 0, 0, 0.2), 0px 1px 1px rgba(0, 0, 0, 0.14),
      0px 1px 3px rgba(0, 0, 0, 0.12);
    border-radius: 4px;
}

.name{
    font-style: normal;
  font-weight: bold;
  font-size: 25px;
  line-height: 1.16;
  text-align: center;
  letter-spacing: 0.03em;  
}

.status {
  display: inline-block;  
  margin-right: 20px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
}

.online {
  composes: status;
  background-color: green;
}

.offline {
  composes: status;
  background-color:red;
}