.statistics{
    background-color: azure;
}
.title{
   margin-bottom: 30px;
  font-style: normal;
  font-weight: bold;
  font-size: 36px;
  line-height: 1.16;
  text-align: center;
  letter-spacing: 0.03em;  
}
.statList{
      display: flex;
    justify-content: center;
    background: #ffffff;
    box-shadow: 0px 2px 1px rgba(0, 0, 0, 0.2), 0px 1px 1px rgba(0, 0, 0, 0.14),
      0px 1px 3px rgba(0, 0, 0, 0.12);
    border-radius: 4px;
}
.item{
    display: flex;
    flex-direction: column;
    padding: 15px;
    margin-bottom: 10px;
    margin-right: 10px;
    box-shadow: 0px 2px 1px rgba(0, 0, 0, 0.2), 0px 1px 1px rgba(0, 0, 0, 0.14),
      0px 1px 3px rgba(0, 0, 0, 0.12);
    border-radius: 4px;
}
.item:hover{
    transform: scale(1.2);
    background-color: cadetblue;
}